import * as React from "react";
import { FunctionComponent, useState } from "react";
import FrameComponent from "./frame-component";
import { CardsContext, CardsType } from "../context/CardsContext";

const CardsContainer: FunctionComponent = () => {
  const [cards, setCards] = useState<CardsType>([
    {
      key: "1",
      image: "url(http://localhost:8000/public/pariscard@3x.png)",
      city: "Paris",
      price: "$699",
    },
    {
      key: "2",
      image: "url(http://localhost:8000/public/greececard@3x.png)",
      city: "Greece",
      price: "$1079",
    },
    {
      key: "3",
      image: "url(http://localhost:8000/public/norwaycard@3x.png)",
      city: "Norway",
      price: "$895",
    },
    {
      key: "4",
      image: "url(http://localhost:8000/public/tuscanycard@3x.png)",
      city: "Tuscany",
      price: "$1245",
    },
  ]);
  return (
    <CardsContext.Provider value={{ cards, setCards }}>
      <div
        style={{
          alignSelf: "stretch",
          display: "flex",
          flexDirection: "row",
          flexWrap: "wrap",
          alignItems: "center",
          justifyContent: "space-between",
          columnGap: "20",
          textAlign: "left",
          fontSize: "20px",
          color: "#fff",
          fontFamily: "Roboto",
        }}
      >
        {(cards || []).map((item, index) => (
          <FrameComponent
            key={item.key}
            parisCardBackgroundImage={item.image}
            paris={item.city}
            detailsWidth="99.92px"
            prop={item.price}
            divWidth="unset"
          />
        ))}
      </div>
    </CardsContext.Provider>
  );
};

export default CardsContainer;
