import * as React from "react";
import { useContext, FunctionComponent } from "react";
import RecCard1 from "./rec-card1";
import { HolidayCardsContext } from "../context/HolidayCardsContext";

const RecCardsContainer: FunctionComponent = () => {
  const holidayCardsContext = useContext(HolidayCardsContext);
  const holidayCards = holidayCardsContext && holidayCardsContext!.holidayCards;
  return (
    <div
      style={{
        alignSelf: "stretch",
        display: "flex",
        flexDirection: "row",
        alignItems: "flex-start",
        justifyContent: "flex-start",
        marginLeft: "-16px",
        marginRight: "-16px",
      }}
    >
      {(holidayCards || []).map((item, index) => (
        <RecCard1
          key={item.key}
          image={item.image}
          city={item.city}
          bookingID={item.bookingId}
          price={item.price}
        />
      ))}
    </div>
  );
};

export default RecCardsContainer;
