import { createContext } from "react";

export type CardsType = Array<{
  key: string;
  image: string;
  city: string;
  price: string;
}>;

export type CardsContextType = {
  cards: CardsType;
  setCards: (value: CardsType) => void;
};

export const CardsContext = createContext<CardsContextType | null>(null);
