import * as React from "react";
import { FunctionComponent, useMemo, type CSSProperties } from "react";

type FrameComponentType = {
  paris?: string;
  prop?: string;

  /** Style props */
  parisCardBackgroundImage?: CSSProperties["backgroundImage"];
  detailsWidth?: CSSProperties["width"];
  divWidth?: CSSProperties["width"];
};

const getStyleValue = (key: string, value: string | number | undefined) => {
  if (value === undefined || value === "") return;
  return { [key]: value };
};

const FrameComponent: FunctionComponent<FrameComponentType> = ({
  parisCardBackgroundImage,
  paris,
  detailsWidth,
  prop,
  divWidth,
}) => {
  const parisCardStyle: CSSProperties = useMemo(() => {
    return {
      ...getStyleValue("backgroundImage", parisCardBackgroundImage),
    };
  }, [parisCardBackgroundImage]);

  const detailsStyle: CSSProperties = useMemo(() => {
    return {
      ...getStyleValue("width", detailsWidth),
    };
  }, [detailsWidth]);

  const divStyle: CSSProperties = useMemo(() => {
    return {
      ...getStyleValue("width", divWidth),
    };
  }, [divWidth]);

  return (
    <div
      style={{
        flex: "1",
        height: "182px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "space-between",
        padding: "0px 10px",
        boxSizing: "border-box",
        marginTop: "20",
        textAlign: "left",
        fontSize: "20px",
        color: "#fff",
        fontFamily: "Roboto",
      }}
    >
      <div
        style={{
          alignSelf: "stretch",
          height: "182px",
          overflow: "hidden",
          flexShrink: "0",
          display: "flex",
          flexDirection: "column",
          alignItems: "flex-start",
          justifyContent: "flex-start",
          padding: "0px 0.29296875px",
          boxSizing: "border-box",
          backgroundImage: "url('/pariscard@3x.png')",
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
          backgroundPosition: "top",
          ...parisCardStyle,
        }}
      >
        <div
          style={{
            alignSelf: "stretch",
            display: "flex",
            flexDirection: "row",
            alignItems: "flex-start",
            justifyContent: "flex-start",
            padding: "8px 12px",
          }}
        >
          <b
            style={{ flex: "1", position: "relative", letterSpacing: "0.02em" }}
          >
            {paris}
          </b>
          <div
            style={{
              width: "90.92px",
              height: "59px",
              display: "flex",
              flexDirection: "column",
              alignItems: "flex-end",
              justifyContent: "flex-start",
              padding: "0px 0.000003814697265625px 0px 0px",
              boxSizing: "border-box",
              gap: "2px",
              textAlign: "right",
              fontSize: "16px",
              ...detailsStyle,
            }}
          >
            <div
              style={{
                position: "relative",
                letterSpacing: "0.04em",
                textTransform: "uppercase",
                display: "inline-block",
                width: "52.53px",
              }}
            >
              from
            </div>
            <div
              style={{
                position: "relative",
                fontSize: "38px",
                lineHeight: "38px",
                fontFamily: "'Baloo Bhai'",
                display: "inline-block",
                width: "90.92px",
                ...divStyle,
              }}
            >
              {prop}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FrameComponent;
